<template>
	<!--pages/customer/whlist/whlist.wxml-->
	<view class="container">
		<view class="cus-list" v-if="whList.length > 0">
			<view class="cusBox" v-for="item in whList" :key="item.id">
				<view class='common' style="background:linear-gradient(to right, #fc6c64, #fc9e5e)">
					<view class='cusli'>
						<image :src="item.loginPassword"></image>
					</view>
					<view class='cusli'>
						<view class="li">{{item.nickname || ''}}</view>
					</view>
				</view>
			</view>
		</view>
		<view class="empty-view" v-if="whList.length <= 0">
			<image class="icon" src="/static/images/allorder.png"></image>
			<text class="text">无粉丝</text>
		</view>
	</view>


</template>

<script>
	const util = require('../../../util/util.js');
	const api = require('../../../config/api.js');

	export default {
		data() {
			return {
				whList: []
			}
		},
		methods: {
			getWhList() {
				util.request(api.FansList, {}, 'POST').then((res)=>{
					if (res.errno === 0) {
						this.whList = res.data;
						uni.hideLoading();
					}
					//wx.hideNavigationBarLoading() //完成停止加载
					//wx.stopPullDownRefresh() //停止下拉刷新
				});
			}
		},
		onShow() {
			uni.showLoading({
				title: '加载中...'
			});
			this.getWhList();
		},
		onReachBottom() {
			console.log("下一页");
			this.getWhList();
		},
		onPullDownRefresh() {
			// 增加下拉刷新数据的功能
			//wx.showNavigationBarLoading();
			this.getWhList();
		}
	}
</script>

<style>
	/* pages/customer/whlist/whlist.wxss */
